@import "./sonar.css";

.jumbotron {
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(255,255,255,0)), color-stop(66.666%, rgb(255,255,255)));
  background: -webkit-linear-gradient(bottom, rgba(255,255,255,0) 0%, rgb(255,255,255) 66.666%);
  background: -o-linear-gradient(bottom, rgba(255,255,255,0) 0%, rgb(255,255,255) 66.666%);
  background: linear-gradient(to top, rgba(255,255,255,0) 0%, rgb(255,255,255) 66.666%);
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 2;
  text-align: left;
}

.jumbotron-content {
  margin: 16px auto;
  max-width: 1280px;
  padding: 80px 40px 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.jumbotron img {
  display: block;
  width: 320px;
}

.jumbotron h1 {
  color: #673c90;
  font-size: 54px;
  line-height: 1.2;
  font-weight: 300;
  text-shadow: 1px 1px 1px #efefef;
  margin: 16px 0;
}

.jumbotron h1 > em,
.jumbotron h1 > span {
  font-weight: 300;
}

.jumbotron h1 > strong {
  font-weight: 500;
  letter-spacing: 0.01em;
}


.showcase {
  position: relative;
  height: calc(75vh);
  display: block;
  /* margin: 0; */
  background-color:#efefef;
}

.showcase .wgsa-homepage__content {
  height: 100%;
}

.showcase .leaflet-container {
  background: #efefef;
}

.showcase .leaflet-control-attribution {
  float: right !important;
  text-align: right;
  background: rgba(239, 239, 239, 0.5);
  font-size: 10px;
}

.showcase .leaflet-control-attribution a {
  color: #673c90;
}

.showcase .mdl-spinner {
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -14px;
}

.showcase__link {
  width: 16px;
  height: 16px;
  margin-left: -8px;
  margin-top: -8px;
  background-color: #3c7383;
  position: absolute !important;
  border-radius: 999px;
  display: block;
  z-index: 1;
  cursor: pointer;
  opacity: 0.5;
  -webkit-transition: background-color 0.28s cubic-bezier(.4,0,.2,1);
  -o-transition: background-color 0.28s cubic-bezier(.4,0,.2,1);
  transition: background-color 0.28s cubic-bezier(.4,0,.2,1);
}

.showcase__link.active {
  background-color: #673c90;
  opacity: 0.8;
  z-index: 1;
}

.showcase__link.inactive {
  z-index: 0;
}

.showcase footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
}

.wgsa-showcase-tooltip-wrapper {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 96px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 0;
}

.wgsa-showcase-tooltip {
  padding: 16px;
  color: #555;
  background-color: rgba(239, 239, 239, 0.54);
  border-radius: 2px;
  position: absolute; /* Creates "morph" fade transition */
}

.wgsa-showcase-tooltip > * {
  margin: 0;
}

.wgsa-showcase-tooltip h3 {
  font-size: 20px;
  line-height: 1.2;
}

.wgsa-showcase-tooltip-enter {
  opacity: 0;
}

.wgsa-showcase-tooltip-enter.wgsa-showcase-tooltip-enter-active {
  opacity: 1;
  -webkit-transition: opacity .28s cubic-bezier(0.165,0.840,0.440,1.000);
  -o-transition: opacity .28s cubic-bezier(0.165,0.840,0.440,1.000);
  transition: opacity .28s cubic-bezier(0.165,0.840,0.440,1.000);
}

.wgsa-showcase-tooltip-leave {
  opacity: 1;
}

.wgsa-showcase-tooltip-leave.wgsa-showcase-tooltip-leave-active {
  opacity: 0;
  -webkit-transition: opacity .28s cubic-bezier(0.165,0.840,0.440,1.000);
  -o-transition: opacity .28s cubic-bezier(0.165,0.840,0.440,1.000);
  transition: opacity .28s cubic-bezier(0.165,0.840,0.440,1.000);
}



