.spectacle-slide {
  background-image: url('/cgps_logo.png');
  background-position: calc(100% - 40px) 32px;
  background-repeat: no-repeat;
  background-size: 320px;
}

.cgps-logo {
  background-image: url('/cgps-logo.svg');
  background-position: calc(100% - 40px) 32px;
  background-repeat: no-repeat;
  background-size: 320px;
}

.logo-top-left {
  background-position: 40px 32px;
}

.spectacle-content {
  max-width: none !important;
}

.full-width-slide,
.full-width {
  height: 100%;
  padding: 0 !important;
  -ms-flex-item-align: start;
      align-self: flex-start;
  max-height: none !important;
}

@media (min-width: 1000px) {
  .full-width-slide > * {
    margin-left: calc(-50vw + 500px);
    margin-right: calc(-50vw + 500px);
  }
}

h1, h2, h3 {
  font-weight: 500;
  letter-spacing: 0.02em;
}

ul {
  padding: 0;
}

li {
  list-style: none;
  font-size: 56px;
  margin-bottom: 56px;
}

.evenly-spaced {
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-pack: center;
      justify-content: center;
}


.cgps-slide-logo {
  position: fixed;
  z-index: 1;
  right: 16px;
  bottom: 16px;
  width: 400px;
}

.flex-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
}

.twitter-tweet-wrapper {
  width: 500px !important;
  opacity: 0;
  -webkit-transition: 0.2s opacity 0.3s ease-in-out;
  -o-transition: 0.2s opacity 0.3s ease-in-out;
  transition: 0.2s opacity 0.3s ease-in-out;
  margin: 0 auto;
}

.flex-group > * {
  margin: 0 16px;
}

.twitter-tweet-wrapper:not(.rendered) > .twitter-tweet {
  display: none;
}

.twitter-tweet-wrapper.rendered {
  opacity: 1;
}


.pw-homepage-cgmlst-organisms {
  font-family: Roboto, arial, sans-serif;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 32px auto;

  /* display: grid;
  grid-template-columns: repeat(1fr, 3); */
  /* max-width: 800px; */
}

.pw-homepage-cgmlst-organisms li {
  border-radius: 3px;
  font-size: 32px;
  font-style: italic;
  margin: 0 8px 1em;
  min-width: 160px;
  padding: 8px 0;
  text-align: center;
  white-space: nowrap;
  width: calc(33.333% - 16px);
}

.pw-homepage-cgmlst-organisms .who-priority {
  font-weight: 500;
}

.pw-homepage-cgmlst-organisms .who-priority.critical {
  background-color: #c0504d;
}

.pw-homepage-cgmlst-organisms .who-priority.high {
  background-color: #ffc000;
  color: #222;
}

.pw-homepage-cgmlst-organisms .who-priority.medium {
  background-color: #ffff00;
  color: #222;
}

.underline {
  -webkit-text-decoration: underline rgba(255, 255, 255, 0.54);
          text-decoration: underline rgba(255, 255, 255, 0.54);
}

.underline-purple {
  -webkit-text-decoration: underline rgba(103, 60, 144, 0.54);
          text-decoration: underline rgba(103, 60, 144, 0.54);
}

.underline-turquoise {
  -webkit-text-decoration: underline rgba(60, 115, 131, 0.54);
          text-decoration: underline rgba(60, 115, 131, 0.54);
}


.infrastructure {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
}

.infrastructure > :last-child {
  grid-row-start: 2;
  grid-column-start: 2;
}

.infrastructure img {
  display: block;
  margin: 0 auto;
}

.infrastructure span {
  font-size: 80px;
}

.acknowledgements {
  margin-right: 80px;
  -ms-flex-item-align: end;
      align-self: flex-end;
}

.acknowledgements img {
  width: 160px;
  margin: 0 auto 8px;
}

.acknowledgements p {
  font-size: 80%;
}

.acknowledgements .person {
  margin-bottom: 24px;
  display: grid;
  grid-template-rows: 1;
  grid-template-columns: 2;
}

.funding {
  position: absolute;
  bottom: 64px;
  left: 64px;
}

.funding img {
  max-width: 320px;
  max-height: 160px;
}

.funding img + img {
  margin-left: 64px;
}

.demo {
  background: #222;
}


